<script>
  import { getContext } from "svelte/internal";
  import InlineSkeleton from "../UI/InlineSkeleton.svelte";
  import SkeletonRow from "./SkeletonRow.svelte";
  import TableRow from "./TableRow.svelte";

  const { leaders, user_bet, game } = getContext("tournamentContext");

  const cols = game.is_active ? "10% 60% 15% 15%" : "10% 45% 22.5% 22.5%";

  const isUserAfterList = user_bet.tournament_id > 12;
  const formatedLeaders = leaders.slice(3, 13 - isUserAfterList);

  const skeletonData = [...Array(8).keys()];
</script>

<div class="tournamentTable">
  <div class="wrapper">
    <div class="scroll-table" data-simplebar>
      <table style="--table-cols: {cols};">
        <thead>
          <tr>
            {#if game.is_active}
              <th>Место</th>
              <th>Имя игрока</th>
              <th>Ставок</th>
              <th>Награда</th>
            {:else}
              <th><InlineSkeleton width="44" /></th>
              <th><InlineSkeleton width="230" /></th>
              <th><InlineSkeleton width="151" /></th>
              <th><InlineSkeleton width="151" /></th>
            {/if}
          </tr>
        </thead>
        <tbody>
          {#if game.is_active}
            {#each formatedLeaders as row (row.id)}
              <TableRow
                showSkeleton={!game.is_active}
                isActiveUser={row.tournament_id === user_bet.tournament_id}
                tournament_id={row.tournament_id}
                avatar={row.user.avatar}
                rank_id={row.user.rank_id}
                username={row.user.username}
                bet_sum={row.bet_sum}
                won_amount={row.won_amount}
              />
            {/each}
            {#if isUserAfterList}
              <TableRow
                isActiveUser={true}
                tournament_id={user_bet.tournament_id}
                avatar={user_bet.user.avatar}
                rank_id={user_bet.user.rank_id}
                username={user_bet.user.username}
                bet_sum={user_bet.bet_sum}
                won_amount={user_bet.won_amount}
              />
            {/if}
          {:else}
            {#each skeletonData as _, i (i)}
              <SkeletonRow />
            {/each}
          {/if}
        </tbody>
      </table>
    </div>
  </div>
</div>

