<script>
  import Timer from "./Timer.svelte";
  import { createEventDispatcher } from "svelte";
  import ProgressBar from "../UI/ProgressBar/ProgressBar.svelte";

  const dispatch = createEventDispatcher();

  export let secondsLeft = 0;
  export let secondsTotal = 0;
  export let color = "var(--gradient-yellow)";
  export let shadowColor = "var(--color-yellow)";
  export let bgColor = "rgba(255, 255, 255, 0.3)";
  export let showLabels = true;
  export let showSeconds = false;
  export let showDays = true;

  $: progressPercent =
    secondsTotal == 0 ? 0 : Math.floor((secondsLeft / secondsTotal) * 100);
</script>

<div class="time">
  <div class="time__header">
    <Timer
      seconds={secondsLeft}
      on:tick={() => dispatch("tick")}
      {...{ showLabels, showSeconds, showDays }}
    />
  </div>
  <ProgressBar
    --progress-percent={`${progressPercent}%`}
    --color={color}
    --shadow-color={shadowColor}
    --bg-color={bgColor}
  />
</div>

<style>
  .time {
    text-align: center;
    font-weight: 600;
    font-size: 12px;
    line-height: 155%;
    color: var(--color-white);
    margin-bottom: 15px;
  }

  .time__header {
    margin-bottom: 12px;

    font-weight: 600;
    font-size: 12px;
    line-height: 155%;
    color: var(--color-white);
  }
</style>
