<script>
  import Portal from "svelte-portal";

  export let heading = "";
  export let isOpened = false;
  export let dialogClass = "";
  export let disableBG = false;

  export function open() {
    isOpened = true;
  }

  export function close() {
    isOpened = false;
  }
</script>

<svelte:head>
  {#if isOpened}
    <style>
      body {
        overflow-y: hidden;
      }
      .modal-backdrop {
        opacity: 0.7;
        visibility: visible;
      }
    </style>
  {/if}
</svelte:head>
<svelte:window on:clickOutsideModal={close} />
<Portal target="#modals">
  {#if isOpened}
    <div class="modal-window" class:active={isOpened} style={$$props.style}>
      <div class="modal-dialog {dialogClass}">
        <div
          class="modal htp-modal narrow-modal"
          style={`
            ${disableBG ? "background: unset" : ""}
          `}
        >
          {#if heading}
            <div class="heading">
              {heading}
              <span on:click={close} class="myicon-close modal-close" />
            </div>
          {:else}
            <div class="closeIcon">
              <span on:click={close} class="myicon-close modal-close" />
            </div>
          {/if}
          <slot />
        </div>
      </div>
    </div>
  {/if}
</Portal>

<style>
  :global(.confirm-buttons) {
    margin-top: 15px;
  }

  .closeIcon {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
  }
</style>
