<script>
  export let handler;
  export let hasOutline = true;
  export let disabled = false;
  export let color = "accent";
  export let disableBorder = false;
  export let customBoxShadow = null;

  let className = `dark_btn ${
    hasOutline ? "has-gradient-border dark-btn_outlined" : ""
  } ${$$props.class ?? ""}`;
</script>

<button
  {disabled}
  class={`${className} ${disableBorder ? "buttonWithDisabledBorder" : ""}
  ${customBoxShadow ? "buttonWithCustomHover" : ""}`}
  on:click={handler}
  style={`
  ${$$props.style}
  --custom-boxshadow: ${customBoxShadow ?? ""}
`}
>
  <slot />
</button>

<style>
  button:disabled {
    pointer-events: none;
  }

  .buttonWithDisabledBorder:after {
    content: unset;
  }

  .buttonWithCustomHover:hover {
    box-shadow: var(--custom-boxshadow);
  }
</style>
