<script>
  import { getContext } from "svelte";
  import ProgressTimer from "../../../components/common/ProgressTimer.svelte";
  import NumberOfPrizes from "./NumberOfPrizes.svelte";
  import TournamentInfo from "./TournamentInfo.svelte";
  import WinAmount from "./WinAmount.svelte";

  const { game } = getContext("tournamentContext");

  const wonAmount = game.prizes.reduce((acc, curValue) => {
    return acc + curValue;
  }, 0);

  $: time_left = game.is_active
    ? Math.floor(
        (new Date(game.deadline).getTime() - new Date().getTime()) / 1000
      )
    : 0;
  $: totalTime = game.is_active
    ? Math.floor(
        (new Date(game.deadline).getTime() -
          new Date(game.created_at).getTime()) /
          1000
      )
    : 0;
</script>

{#if game.is_active}
  <div class="tournamentHeader">
    <TournamentInfo numberOfPrizes={game.prizes.length} />
    <WinAmount {wonAmount} />

    <div class="timerWrapper">
      <div class="numberOfPrizesWrapper">
        <NumberOfPrizes numberOfPrizes={game.prizes.length} />
      </div>
      <div class="timerWrapperProgress">
        <div class="timerTitle">Турнир кончится через</div>
        <ProgressTimer
          showDays={time_left > 60}
          showSeconds={time_left <= 60}
          on:tick={() => time_left--}
          secondsLeft={time_left}
          secondsTotal={totalTime}
          bgColor={`#2e3038`}
        />
      </div>
    </div>
  </div>
{:else}
  <div class="tournamentHeaderInactive">
    <div class="tournamentHeaderInactiveImage" />
    <div class="tournmentDidntStart">
      <span class="tournmentDidntStartTitle">Турнир еще не начался</span>
      <span class="tournmentDidntStartDescription">
        Следите за новостями в наших группах, когда начнется
      </span>
    </div>
    <div class="tournamentInfoWrapper">
      <TournamentInfo isActiveTournament={false} />
    </div>
  </div>
{/if}

