<script>
  import { getContext } from "svelte/internal";
  import WinnerCard from "./WinnerCard.svelte";

  const { leaders, game, user_bet } = getContext("tournamentContext");

  const winners = leaders.slice(0, 3);

  const skeletonData = [...Array(3).keys()];
</script>

<div class="top3Winners">
  {#if game.is_active}
    {#each winners as winner (winner.id)}
      <WinnerCard
        {winner}
        isUser={user_bet.tournament_id === winner.tournament_id}
      />
    {/each}
  {:else}
    {#each skeletonData as _, i (i)}
      <WinnerCard
        isInactiveGame={true}
        user={i === 0 ? user_bet?.user : null}
      />
    {/each}
  {/if}
</div>

<style>
  .top3Winners {
    display: grid;
    column-gap: 18px;
    grid-template-columns: 1fr 1fr 1fr;
    margin-bottom: 38px;
  }

  @media (max-width: 1400px) {
    .top3Winners {
      column-gap: unset;
      justify-content: center;
      row-gap: 38px;
      grid-template-columns: minmax(auto, 500px);
      grid-template-rows: repeat(3, auto);
    }
  }
</style>
