<script>
  import { padNumber } from "../../helpers/Strings.js";
  import { createEventDispatcher, onDestroy } from "svelte";

  const dispatch = createEventDispatcher();

  export let seconds;
  export let showLabels = false;

  export let showDays = false;
  export let showSeconds = true;

  $: d = Math.floor(seconds / 86400);
  $: h = Math.floor((seconds - (showDays ? d * 86400 : 0)) / 3600);
  $: m = Math.floor((seconds - (showDays ? d * 86400 : 0) - h * 3600) / 60);
  $: s = seconds - (showDays ? d * 86400 : 0) - h * 3600 - m * 60;

  function updateTimer() {
    dispatch("tick", {
      seconds: seconds,
    });
  }

  onDestroy(() => {
    clearInterval(interval);
  });

  let interval = setInterval(updateTimer, 1000);
  $: if (seconds <= 0) {
    clearInterval(interval);
  }
</script>

<div class="countdown">
  {#if showDays}
    <span class="countdown__days">
      {padNumber(d)}
      {#if showLabels}
        <span class="countdown__label">д</span>
      {/if}
    </span>
    <span class="countdown__delimiter">:</span>
  {/if}
  <span class="countdown__hours">
    {padNumber(h)}
    {#if showLabels}
      <span class="countdown__label">ч</span>
    {/if}
  </span>
  <span class="countdown__delimiter">:</span>
  <span class="countdown__minutes">
    {padNumber(m)}
    {#if showLabels}
      <span class="countdown__label">м</span>
    {/if}
  </span>

  {#if showSeconds}
    <span class="countdown__delimiter">:</span>
    <span class="countdown__seconds">
      {padNumber(s)}
      {#if showLabels}
        <span class="countdown__label">c</span>
      {/if}
    </span>
  {/if}
</div>
