<script>
  import Modal from "../../../components/common/Modal.svelte";
  import Button from "../../../components/UI/Button/Button.svelte";

  let modal;
  let step = 1;

  export function open() {
    modal.open();
    step = 1;
  }

  export function close() {
    modal.close();
  }

  const onButtonClick = () => {
    if (step === 2) {
      modal.close();
      return;
    }

    step++;
  };

  const titleMapper = {
    1: "Делайте ставки",
    2: "Результат",
  };
  const descriptionMapper = {
    1: "Каждая ставка, сделанная на коэффициент от х1.5 идет в вашу турнирную статистику. Фриспины в зачет не идут",
    2: "В момент завершения турнира, топ игроков разделят между собой банк. Для получения выигрыша онлайн быть не обязательно!",
  };
  const buttonMapper = {
    1: "Далее",
    2: "Я понял",
  };
  const imageMapper = {
    1: "slotMachine.png",
    2: "stonks.png",
  };
</script>

<Modal bind:this={modal} disableBG={true} dialogClass={"modal-w500"}>
  <div class="bluredBackground">
    <div class="howToJoinTitle">Как участвовать?</div>
    <div
      class="howToJoinImageBackground {step === 1
        ? 'howToJoinImageBackgroundRed'
        : 'howToJoinImageBackgroundPurple'}"
    >
      <img
        class="howToJoinImage {`howToJoinImageStep${step}`}"
        src={`assets/images/${imageMapper[step]}`}
        alt={"image"}
      />
    </div>
    <div class="howToJoinTextTitle">{titleMapper[step]}</div>
    <div class="howToJoinTextDescripton">
      {descriptionMapper[step]}
    </div>

    <Button
      handler={onButtonClick}
      style={`
        min-height: 48px;
        width: 100%;
      `}
    >
      <div class="tournamentInfoButtonContent">
        {buttonMapper[step]}
        <span class="myicon-arrow-circle-right" />
      </div>
    </Button>

    <div class="howToJoinDots">
      <div class="howToJoinDot {step === 1 ? 'howToJoinDotActive' : ''}" />
      <div class="howToJoinDot {step === 2 ? 'howToJoinDotActive' : ''}" />
    </div>
  </div>
</Modal>

