  }

  .howToJoinImageBackgroundRed {
    background: linear-gradient(25.87deg, #fe927b 17.59%, #ff3939 111.93%);
  }

  .howToJoinImageBackgroundPurple {
    background: linear-gradient(45.87deg, #6f7dff 6.6%, #8729ff 102.94%);
