    font-size: 16px;
    line-height: 155%;
    color: var(--color-gray);
  }

  .tournamentInfoButtonContent {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .tournamentInfoButtonContent span {
    margin-left: 8px;
  }

  .howToJoinDots {
    margin: 26px auto 0 auto;
    height: 12px;
    display: grid;
    align-items: center;
    width: max-content;
    grid-template-columns: 1fr 1fr;
    column-gap: 9px;
  }

  .howToJoinDot {
    border-radius: 50%;
    width: 5px;
    height: 5px;
